#root {
    height: 100%;
}

.App {
    height: 100%;
    display: flex;
    flex-direction: row;
}

.App .PokeHeader {
  height: 15%;
}

.App > div:nth-of-type(2) {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

.App > div:nth-of-type(2) > div:nth-of-type(2){
  height: 85%;
}

.App .PokeHeader {
  text-align: center;
}

/****GLOBAL RULES FOR CharacterInformation***/
.CharacterInformation > div:nth-of-type(1) h3:nth-child(2){
  margin-left: auto;
}

/***GLOBAL RULES FOR ProfileOverview IN MenuToggler***/
.MenuToggler .ProfileOverview{
  width: 100%;
  max-width: 400px;
  height: 50px;
}

.MenuToggler .ProfileOverview .Avatar  img {
  position: relative;
  left: -32px;
  top: -36px;
  width: 121px;
  height: 121px;
}

.MenuToggler .CharacterInformation {
  width: 55%;
  height: 100%;
}

.MenuToggler .ProfileOverview .CharacterInformation .Bar {
  margin-left: 1em;
  margin-bottom: 0.1em;
}

.MenuToggler .ProfileOverview .Avatar {
  display: flex;
  width: 20%;
}

.MenuToggler .ProfileOverview h3 {
  margin: 0.05em;
  margin-left: 16px;
  padding: 0;
  color: white;
  font-size: 50%;
}

.Profile .MenuToggler .menuItem, .QuestDetails .MenuToggler .menuItem{
  margin: 0.2em;
}

.Profile .MenuToggler, .QuestDetails .MenuToggler{
  max-width: 400px;
}

@media screen and (max-width: 500px) {
  .App .MenuToggler {
    width: 90%;
  }
}
